import React, { useState, useEffect } from 'react';
import { useToasts } from 'react-toast-notifications';

import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';
import { useDispatch } from 'react-redux';
import { actions } from '../redux/_actions';

const Order = (props) => {
  const [cards, setCards] = useState([]);

  const dispatch = useDispatch();
  const { addToast } = useToasts();

  const showToast = (message, appearance = 'error') => {
    addToast(message, { appearance, autoDismiss: true });
  }

  useEffect(() => {
    loadCards();
  }, [])

  const loadCards = () => {
    dispatch(actions.setLoading(true));
    api.getOrderedProducts()
      .then((data) => {
        dispatch(actions.setLoading(false));
        if (!data || data.error) {
          return showToast(data.error);
        }
        setCards(data.products);
      })
      .catch(ex => {
        console.log(ex);
        showToast('Failed loading items');
      })
  };

  const showProductPage = (url) => {
    window.open(url, '_blank');
  }

  return (
    <Layout>
      <div>
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.map((card, i) => (
            <div key={i} className={'shop-item'} onClick={() => showProductPage(card.url)}>
              <Card card={card} showPrice={true} orderStatus={card.orderStatus} />
            </div>
          ))}
        </div>
      </div>
      {!cards.length &&<NoCard page="order" />}
    </Layout>
  );
};

export default Order;
